.Searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  /* color: #fff; */
  /* background-color: #3f51b5; */
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    
}

.SearchForm {
  display: flex;
  align-items: baseline;
  width: 100%;
  max-width: 600px;
  /* background-color: #fff; */
  border-radius: 3px;
  overflow: hidden;
      border-bottom: 1px solid #cacaca;

  
}

.SearchForm_button {
    /* background-color: #fff; */

  display: inline-block;
  width: 48px;
  height: 48px;
  border: none;
  /* background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg'); */
  /* background-size: 40%; */
  /* background-repeat: no-repeat; */
  /* background-position: center; */
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
   background-color: #162133;
   color: tomato
}

.SearchForm_button:hover {
  opacity: 1;
}


.SearchForm_button-label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.SearchForm_input {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
  /* background-color: red; */
 
   background-color: #162133;
    color: #cacaca;
    /* border-bottom: 1px solid #cacaca; */
    /* padding: 10px; */
;
       
}

.SearchForm_input::placeholder {
  font: inherit;
  font-size: 18px;
